<template>
  <div class="calculator">
    <input
      type="text"
      class="calculator__inp"
      placeholder="0"
      v-model="result"
    />
    <div class="calculator__btns">
      <button
        class="calculator__btn"
        v-for="calculatorElement in calculatorElements"
        :key="calculatorElement"
        :class="{
          btn__bg_green: ['C', '*', '/', '-', '+', '%', '='].includes(
            calculatorElement
          ),
        }"
        @click="action(calculatorElement)"
      >
        {{ calculatorElement }}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: "",
      calculatorElements: [
        "C",
        "*",
        "/",
        "-",
        7,
        8,
        9,
        "+",
        4,
        5,
        6,
        "%",
        1,
        2,
        3,
        "=",
        0,
        ".",
      ],
      previousCalculatorValue: "",
      operator: null,
    };
  },
  methods: {
    action(n) {
      if (!isNaN(n) || n === ".") {
        this.result += n + "";
      } else if (n === "C") {
        this.result = "";
      } else if (n === "%") {
        this.result = this.result / 100 + "";
      } else if (["/", "*", "-", "+"].includes(n)) {
        this.operator = n;
        this.previousCalculatorValue = this.result;
        this.result = "";
      } else if (n === "=") {
        this.result = eval(
          this.previousCalculatorValue + this.operator + this.result
        );
        this.previousCalculatorValue = "";
        this.operator = null;
      }
    },
  },
};
</script>

